import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import cx from 'classnames';
import './Header.css';

import TearingCloth from '../Cloth';

class Header extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isAboutVisible: false
    }
    this.mountCanvas = this.mountCanvas.bind(this);
    this.handleAboutClick = this.handleAboutClick.bind(this);
    this.handleAboutMouseLeave = this.handleAboutMouseLeave.bind(this);

    this.canvas = null;
    this.cloth = null;
  }
  render() {
    const { title } = this.props;
    const { isAboutVisible } = this.state;

    const aboutClassNames = cx({
      'Header-About': true,
      'Header-About--show': isAboutVisible
    })

    const about =
      <div className={aboutClassNames} onMouseLeave={this.handleAboutMouseLeave}>
        <canvas ref={this.mountCanvas}></canvas>
        <div className="Header-AboutContent">
          <h1>Seznam is a curated list that helps me keep track of artist and friends whose work I admire, and expose them to others.</h1>
          <h1>If you want to become part of the list or a friend, please let me know by <a href="mailto:mail@klemenilovar.com">email</a> or follow me on <a href="https://www.instagram.com/klemen_ilovar/" target="_blank">Instagram</a>.</h1>
          <h1>Run by<br /><a href="https://klemenilovar.com/" target="_blank">Klemen Ilovar</a></h1>
          <h1>Built by<br /><a href="https://jure.io/" target="_blank">Jure Martinec</a></h1>
          <h1>Both part of<br /><a href="http://ansambel.org/" target="_blank">Ansambel</a></h1>
          <h1>2017</h1>
        </div>
      </div>;

    return (
      <div className="Header">
        <div className="Header-Main">
          {about}
          <h1 onMouseEnter={this.handleAboutClick}>Seznam, odpri se!</h1>
          {title && <h1>{title}</h1>}
        </div>
        <div className="Header-Nav">
          <NavLink to="/index">Index </NavLink>
          <NavLink to="/" className="Header-NavHome"> Images</NavLink>
        </div>
      </div>
    );
  }
  componentDidMount() {
    this.initCanvas();
  }
  mountCanvas(canvas) {
    this.canvas = canvas;
  }
  initCanvas() {
    const { canvas } = this;

    const parentDimensions = canvas.parentNode.getBoundingClientRect();
    console.log(parentDimensions);
    const width = Math.round(parentDimensions.width/8) * 8;
    const height =  Math.round(parentDimensions.height/8) * 8;

    const boxesInRow = 100;
    const boxSize = Math.floor(width / boxesInRow);

    const clothBoxCountX = Math.floor(width / boxSize);
    const clothBoxCountY = Math.floor(height / boxSize) - Math.floor(128 / boxSize);

    canvas.width = width + 32;
    canvas.height = height + 32;//canvas.parentNode.getBoundingClientRect().height;

    const clothOptions = {
      spacing: boxSize,
      clothX: clothBoxCountX,
      clothY: clothBoxCountY,
      gravity: 1000,
      friction: 0.99,
      bounce: 0,
      // accuracy: 0.5,
      tearDist: 10000,
      gridColor: 'rgba(22,22,22,1)',
      surfaceColor: 'rgba(0,0,0,0.98)',
    }

    this.cloth = new TearingCloth(canvas, clothOptions);
    this.cloth.init();
  }
  handleAboutClick() {
    console.log("dropping");
    this.cloth.drop();
    this.setState({ isAboutVisible: true })
  }
  handleAboutMouseLeave() {
    console.log("raising");
    this.cloth.raise();
    this.setState({ isAboutVisible: false })
  }
}

Header.defaultProps = {
  title: null
}

export default Header;



