let canvas = null;
let ctx = null;
let mouse = {
  cut: 8,
  influence: 26,
  down: false,
  button: 1,
  x: 0,
  y: 0,
  px: 0,
  py: 0
};
let raisePoints = true;

class Point {
  constructor (x, y, options) {
    this.options = options;

    this.x = x
    this.y = y
    this.px = x
    this.py = y
    this.vx = 0
    this.vy = 0
    this.pinX = null
    this.pinY = null

    this.constraints = [];
  }

  update(delta) {
    const { gravity, friction, bounce } = this.options;

    if (this.pinX && this.pinY) return this

    if (mouse.down) {
      let dx = this.x - mouse.x
      let dy = this.y - mouse.y
      let dist = Math.sqrt(dx * dx + dy * dy)

      if (dist < mouse.influence) {
        this.px = this.x - (mouse.x - mouse.px)
        this.py = this.y - (mouse.y - mouse.py)
      } else if (dist < mouse.cut) {
        this.constraints = []
      }
    }

    if(raisePoints) {
    //   // this.y = 0;// getRandomIntInclusive(-1,1);
      this.addForce(0, -5000);
    }

    this.addForce(0, gravity)

    let nx = this.x + (this.x - this.px) * friction + this.vx * delta
    let ny = this.y + (this.y - this.py) * friction + this.vy * delta

    this.px = this.x
    this.py = this.y

    this.x = nx
    this.y = ny

    this.vy = this.vx = 0

    if (this.x >= canvas.width) {
      this.px = canvas.width + (canvas.width - this.px) * bounce
      this.x = canvas.width
    } else if (this.x <= 0) {
      this.px *= -1 * bounce
      this.x = 0
    }

    if (this.y >= canvas.height) {
      this.py = canvas.height + (canvas.height - this.py) * bounce
      this.y = canvas.height
    } else if (this.y <= 0) {
      this.py *= -1 * bounce
      this.y = 0
    }

    return this
  }

  draw() {
    let i = this.constraints.length
    while (i--) this.constraints[i].draw()
  }

  resolve () {
    if (this.pinX && this.pinY) {
      this.x = this.pinX
      this.y = this.pinY
      return
    }

    this.constraints.forEach((constraint) => constraint.resolve())
  }

  attach (point) {
    this.constraints.push(new Constraint(this, point, this.options))
  }

  free (constraint) {
    this.constraints.splice(this.constraints.indexOf(constraint), 1)
  }

  addForce (x, y) {
    this.vx += x
    this.vy += y
  }

  pin (pinx, piny) {
    this.pinX = pinx
    this.pinY = piny
  }
}

class Constraint {
  constructor (p1, p2, options) {
    this.options = options;

    this.p1 = p1;
    this.p2 = p2;
    this.length = options.spacing
  }

  resolve () {
    const { tearDist } = this.options;

    let dx = this.p1.x - this.p2.x
    let dy = this.p1.y - this.p2.y
    let dist = Math.sqrt(dx * dx + dy * dy)

    if (dist < this.length) return

    let diff = (this.length - dist) / dist

    if (dist > tearDist) this.p1.free(this)

    let mul = diff * 0.5 * (1 - this.length / dist)

    let px = dx * mul
    let py = dy * mul

    !this.p1.pinX && (this.p1.x += px)
    !this.p1.pinY && (this.p1.y += py)
    !this.p2.pinX && (this.p2.x -= px)
    !this.p2.pinY && (this.p2.y -= py)

    return this
  }

  draw () {
    ctx.moveTo(this.p1.x, this.p1.y)
    ctx.lineTo(this.p2.x, this.p2.y)
  }
}

class Cloth {
  constructor(options) {
    this.options = options;
    const { clothX, clothY, spacing } = this.options;

    this.points = [];

    let startX = canvas.width / 2 - clothX * spacing / 2

    for (let y = 0; y <= clothY; y++) {
      for (let x = 0; x <= clothX; x++) {
        let xPos = startX + x * spacing// + getRandomIntInclusive(-4,4);
        let yPos = 16 + y * spacing// + getRandomIntInclusive(-2,2);

        let point = new Point(xPos, yPos, this.options);
        y === 0 && point.pin(point.x, point.y);
        x !== 0 && point.attach(this.points[this.points.length - 1]);
        y !== 0 && point.attach(this.points[x + (y - 1) * (clothX + 1)]);

        this.points.push(point)
      }
    }
  }

  update(delta) {
    const { accuracy, spacing, clothX, clothY } = this.options;

    let i = accuracy;

    while (i--) {
      this.points.forEach((point) => {
        point.resolve()
      })
    }

    this.points.forEach((point) => {
      point.update(delta * delta)//.draw();
    });

    const boxesInRow = clothX + 1;
    const rowCount = clothY;
    let rowsDrawn = 0;

    this.points.forEach((point, i) => {
      if(i % boxesInRow === 0) {
        rowsDrawn++;
      }

      const topLeftPoint = point;
      const topRightPoint = this.points[i+1];
      const bottomRightPoint = this.points[i+boxesInRow+1];
      const bottomLeftPoint = this.points[i+boxesInRow];

      const isLastInRow = i !== rowsDrawn*boxesInRow - 1;

      const shouldDrawBox = topRightPoint && bottomRightPoint && bottomLeftPoint && isLastInRow;

      if(shouldDrawBox) {
        ctx.beginPath();

        ctx.moveTo(topLeftPoint.x,topLeftPoint.y);
        ctx.lineTo(topRightPoint.x,topRightPoint.y);
        ctx.lineTo(bottomRightPoint.x,bottomRightPoint.y);
        ctx.lineTo(bottomLeftPoint.x,bottomLeftPoint.y);
        ctx.lineTo(topLeftPoint.x,topLeftPoint.y);

        // ctx.moveTo(topLeftPoint.x - 1,topLeftPoint.y - 1);
        // ctx.lineTo(topRightPoint.x + 1,topRightPoint.y - 1);
        // ctx.lineTo(bottomRightPoint.x + 1,bottomRightPoint.y + 1);
        // ctx.lineTo(bottomLeftPoint.x - 1,bottomLeftPoint.y + 1);
        // ctx.lineTo(topLeftPoint.x - 1,topLeftPoint.y - 1);

        ctx.closePath();
        ctx.fill();
      }
    });

    ctx.beginPath()

    this.points.forEach((point) => {
      point.draw();
    });
    ctx.closePath();
    ctx.stroke();
  }
}

export default class TearingCloth {
  constructor(canvasToInit, options = {}) {
    canvas = canvasToInit;
    ctx = canvas.getContext('2d');
    ctx.strokeStyle = options.gridColor;
    ctx.fillStyle = options.surfaceColor;

    const defaultOptions = {
      accuracy: 5,
      gravity: 400,
      clothY: 32,
      clothX: 56,
      spacing: 8,
      tearDist: 60,
      friction: 0.99,
      bounce: 0.5,
    }

    this.options = Object.assign({}, defaultOptions, options);

    window.requestAnimFrame =
      window.requestAnimationFrame ||
      window.webkitRequestAnimationFrame ||
      window.mozRequestAnimationFrame ||
      window.oRequestAnimationFrame ||
      window.msRequestAnimationFrame ||
      function (callback) {
        window.setTimeout(callback, 1e3 / 60)
      }

    canvas.onmousemove = this.handleMouseMove.bind(this);
    canvas.onmouseout = this.handleMouseOut.bind(this);
    canvas.onmousedown = this.handleMouseDown.bind(this);
    canvas.onmouseup = this.handleMouseUp.bind(this);
    canvas.oncontextmenu = this.handleContextMenu.bind(this);

    this.cloth = new Cloth(this.options);

    this.update = this.update.bind(this);
  }

  handleMouseMove(e) {
    mouse.down = true;
    this.setMouse(e);
  }
  handleMouseOut(e) {
    mouse.down = false;
  }
  handleMouseDown(e) {
    mouse.button = e.which;
    mouse.down = true;
    this.setMouse(e);
  }
  handleMouseUp(e) {
    mouse.down = false;
  }
  handleContextMenu(e) {
    e.preventDefault();
  }

  setMouse (e) {
    let rect = canvas.getBoundingClientRect();
    mouse.px = mouse.x
    mouse.py = mouse.y
    mouse.x = e.clientX - rect.left
    mouse.y = e.clientY - rect.top
  }
  update(time) {
    let { cloth } = this;

    ctx.clearRect(0, 0, canvas.width, canvas.height)
    cloth.update(0.016);

    window.requestAnimFrame(this.update);
  }

  init() {
    window.requestAnimFrame(this.update);
  }

  raise() {
    const { clothX, clothY } = this.options;

    const boxesInRow = clothX;
    const rowCount = clothY;
    let rowsDrawn = 0;

    let forceUnit = 2;

    let force = forceUnit;
    let boxNumberInRow = 0;
    this.cloth.points.forEach((point, i) => {
      // boxNumberInRow = i % boxesInRow === 0 ? 0 : boxNumberInRow + 1;
      //
      // const isLeftSide = boxNumberInRow < boxesInRow/2;
      // const isRightSide = boxNumberInRow >= boxesInRow/2;
      //
      // if(isLeftSide) {
      //   //increase force
      //   force += force;
      //   console.log("left: ", force);
      // } else if(isRightSide) {
      //   //decrease force
      //   force -= force;
      //   console.log("right: ", force);
      // }
      //
      // if(force <= 0) {
      //   force = forceUnit;
      // }

      // point.addForce(0, -force);

    });
    // setTimeout(() => {
      raisePoints = true;
    // }, 1000)
  }
  drop() {
    this.cloth.points.forEach((point) => {
      point.y = 0;
    });
    raisePoints = false;
  }
}

function getRandomIntInclusive(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}



